import React from "react";
import { MDXProvider } from "@mdx-js/react";
import { Link } from "gatsby";
import Children from 'react-children-utilities';

const components = {
  h2: props => <a href={`#${props.children.replace(/\s/g, "-")}`}><h2 className="mt-12 text-3xl text-violet-900 font-semibold" id={props.children.replace(/\s/g, "-")} {...props} /></a>,
  h3: props => <a href={`#${props.children.replace(/\s/g, "-")}`}><h3 className="mt-7 text-2xl text-violet-900 font-semibold" id={props.children.replace(/\s/g, "-")} {...props} /></a>,
  p: props => <p className="mt-5" {...props} />,
  blockquote: props => <blockquote className="mt-5 text-sm border-violet-500 border-l-2 pl-4 pt-1 pb-1"><p>{Children.onlyText(props.children)}</p></blockquote>,
  a: props => <a className="border-b-2 border-violet-300 hover:border-violet-700 text-violet-500" {...props} />,
  hr: props => <hr className="border-violet-400" {...props} />,
  ul: props => <ul className="list-disc ml-5" {...props} />,
  /*li: props => <li {...props} />,*/
  ol: props => <ol className="list-decimal ml-5" {...props} />
}

const PostMDXProvider = ({ children }) => {
  return <MDXProvider components={components}>{children}</MDXProvider>
}

export default PostMDXProvider
