import React from "react";
import { HomeIcon } from "@heroicons/react/24/outline";
import { Link } from "gatsby";

const menus = [{ title: "posts", path: "/" }];

const Layout = ({ children }) => {
  return (
    <>
      <header className="flex justify-between sm:w-full md:w-[768px] m-auto px-4 py-4">
        <Link className="flex" to="/">
          <HomeIcon className="w-8 h-8 align-middle text-violet-900 mr-1" />
          <div className="font-bold text-2xl align-middle text-violet-900">
            jongseong`s blog
          </div>
        </Link>
        <nav>
          <ul className="flex gap-x-2">
            {menus.map((item) => (
              <li key={item.title} className="font-bold text-xl text-violet-900">
                <Link to={item.path}>{item.title}</Link>
              </li>
            ))}
          </ul>
        </nav>
      </header>
      <main className="flex flex-col sm:w-full md:w-[768px] mt-3 m-auto px-4 py-4">
        {children}
      </main>
      <footer>
        <div></div>
      </footer>
    </>
  )
}

export default Layout;
